import s from './styles.module.css'

import home from './img/home.svg';
import catalog from './img/catalog.svg';
import cart from './img/cart.svg';
import favorites from './img/favorites.svg';
import vector from './img/vector.svg';
import { Link } from 'react-router-dom';
import { useAppSelector } from '../../storage/hook';

const dataCategory = [
    {
        title: "Главная",
        icon: home,
        pathname: "/",
    },
    {
        title: "Каталог",
        icon: catalog,
        pathname: "/catalog",
    },
    {
        title: "Корзина",
        icon: cart,
        pathname: "/cart",
    },
    {
        title: "Избранное",
        icon: favorites,
        pathname: "/favorites",
    },
    {
        title: "Профиль",
        icon: vector,
        pathname: "/profile",
    },

]

function TabBar() {
    const favorites = useAppSelector(state => state.products.favoriteProducts) || {length: 0};
    const cartCount = useAppSelector(state => state.cart.totalProductsCount);
    return ( 
        <nav className={s.tabBarWrapper}>
            <ul className={s.list}>
                {dataCategory.map((item, index) => {
                    return (
                        <li key={index}>
                            <Link to={item.pathname} className={s.item}>
                                <img className={s.image} src={item.icon} alt={item.title} />
                                <span className={s.title}>{item.title}</span>

                                {item.pathname === '/favorites' && favorites?.length > 0 && <span className={s.iconBubble}>{favorites.length}</span>}
                                {item.pathname === '/cart' && cartCount > 0 && <span className={s.iconBubble}>{cartCount}</span>}
                            </Link>
                        </li>
                    )
                })}
            </ul>
        </nav>
     );
}

export default TabBar;

