import { Link, useLocation } from "react-router-dom";
import { ContentHeader } from "../../components/content-header";
import s from './styles.module.css';

import Hero from "../../components/hero";
import CategoryMenu from "../../components/category-menu";
import classNames from "classnames";
import Banner, { BannerSize } from "../../components/banner";
//images
import imageGift from './img/gift.png';
import imageGiftMini from './img/gift_mini.png';

import imageSets from './img/set.png';
import imageMix from './img/mix.png';
import imageTurkey from './img/turkey.png';
import imageHorns from './img/horns.png';
import Carousel from "../../components/carousel";
import { useSelector } from "react-redux";
import { useAppSelector } from "../../storage/hook";
import { Card } from "../../components/card";
import { useMediaQueries } from "../../hooks/useMediaQuerry";



const BANNERS = [
    {
        subtitle: "Легкое говяжье - пластины",
        title: "Подарок за первый заказ",
        colorBg: "#FF8F27",
        bg: imageGift
    },
    {
        size: BannerSize.small,
        title: "Наборы",
        subtitle: "для дрессировки",
        colorBg: "#D8A217",
        price: 'от 840 ₽',
        bg: imageSets,
    },
    {
        size: BannerSize.small,
        title: "Микс масел",
        subtitle: "пищевая здоровая натуральная добавка",
        colorBg: "#24B5BE",
        bg: imageMix,
    },
    {
        size: BannerSize.small,
        title: 'Рога северного оленя',
        subtitle: 'от 10 до 30 кг.',
        colorBg: '#9CCD55',
        bg: imageHorns,
    },
    {
        size: BannerSize.small,
        title: 'Слипы из шеи индейки',
        subtitle: '100% натуральное',
        colorBg: '#DB6825',
        bg: imageTurkey,
    },

]

export enum CAROUSEL_CATEGORIES {
    hits = 'hits',
    delicacy = 'delicacy',
    recent = 'recent',
    banners = 'banners'

} 

function MainPage() {

    const cards = useAppSelector(state => state.products.data)
    const { xs, sm } = useMediaQueries();

    const carouselPerView = xs || sm ? 2 : 4;


    return (
        <>
            <Hero />
            <div className={classNames("content container", s.mainPage)}>
                <CategoryMenu extraClass="home" />

                <Banner {...BANNERS[0]} size={xs ? BannerSize.small : BannerSize.big} />

                <Carousel items={cards.slice(0, 10)} perView={carouselPerView} component={Card} title={'Хиты'} category={CAROUSEL_CATEGORIES.hits}/>
                {xs
                    ? <Carousel items={BANNERS.slice(1, 3)} perView={1} component={Banner} title={'Баннеры'} pagination="true" category={CAROUSEL_CATEGORIES.banners}/>
                    : <div className={s.banner_wrapper}>
                        {BANNERS.slice(1, 3).map((banner, index) => <Banner key={index} {...banner} size={xs ? BannerSize.small : BannerSize.big} />)}
                    </div>}
                <Carousel items={cards.slice(10, 20)} perView={carouselPerView} component={Card} title={'Лакомства'} category={CAROUSEL_CATEGORIES.delicacy}/>
                {xs
                    ? <Carousel items={BANNERS.slice(3, 5)} perView={1} component={Banner} title={'Баннеры'} pagination="true" category={CAROUSEL_CATEGORIES.banners}/>
                    : <div className={s.banner_wrapper}>
                    {BANNERS.slice(3, 5).map((banner, index) => <Banner key={index} {...banner} size={xs ? BannerSize.small : BannerSize.middle} />)}
                </div>}
                <Banner {...BANNERS[0]} size={xs ? BannerSize.small : BannerSize.big} />
                <Carousel items={cards.slice(20, 30)} perView={carouselPerView} component={Card} title={'Недавно смотрели'} category={CAROUSEL_CATEGORIES.recent}/>


            </div>



        </>
    );
}

export default MainPage;


