//хук для медиа-запросов
//https://blog.tomaszgil.me/how-to-use-css-media-queries-in-react-components

import { useEffect, useMemo, useState } from "react";

export  function useMediaQuery(query: string) {
    const mediaQuery = useMemo(() => window.matchMedia(query), [query]);
    const [match, setMatch] = useState(mediaQuery.matches);
  
    useEffect(() => {
      const onChange = () => setMatch(mediaQuery.matches);
      mediaQuery.addEventListener("change", onChange);
  
      return () => mediaQuery.removeEventListener("change", onChange);
    }, [mediaQuery]);
  
    return match;
  }

export function useMediaQueries() {

    const xs = useMediaQuery("(max-width: 480px)");
    const sm = useMediaQuery("(min-width: 481px) and (max-width: 767px)");
    const md = useMediaQuery("(min-width: 768px) and (max-width: 1200px)");
    const lg = useMediaQuery("(min-width: 1200px)");
  
    return { xs, sm, md, lg };
  }  