
import './styles.css';
import logoLarge from './assets/logo.svg';
import logoSmall from './assets/logo mobile.svg';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import { DetailedHTMLProps, HTMLAttributes } from 'react';
import { useMediaQueries } from '../../hooks/useMediaQuerry';

interface ILogoProps {
  className?: string, 
  href?: string,
  title?: string
}

export function Logo({className, href='/', ...props}: ILogoProps) {
  const hrefValue = href ? href : null
  const { xs, sm, md, lg } = useMediaQueries()

  function getLogo() {
    if (xs) {
      return logoSmall
    } else {
      return logoLarge
    }

  }

  return (
    hrefValue 
      ? <Link /* replace не добавляет в историю хождения на странице */ to={{pathname: hrefValue}} className={classNames('logo', {className: !!className})} {...props}>
          <img src={getLogo()} alt='logo' className='logo__pic' />
        </Link>
      : <span className={`${className} logo`} {...props}>
          <img src={getLogo()} alt='logo' className='logo__pic' />
        </span>  
        
  );
}
